<script>
export default {
  name: "Home",
  components: {},
  data() {
    return {
      data: [],
      loading: false,
      busy: false,
      current: 0,
      pokemonName: "",
      selectedPokemon: []
    };
  },
  mounted() {
    // this.getAbilityList();
    this.getPokemonList();
  },
  methods: {
    getPokemonList() {
      let data = {
        limit: 10,
        offset: this.current
      };
      this.$api.common.getPokemonList(data).then(async res => {
        let promiseArr = [];
        res.data.results.forEach(async item => {
          let _data = {
            name: item.name
          };

          let promise = this.$api.common.getPokemonInfo(_data);

          await promiseArr.push(promise);
        });

        await Promise.all(promiseArr).then(values => {
          if (values.length > 0) {
            this.data = this.data.concat(values);
          } else {
            this.$message.warning("所有宝可梦已加载完毕");
            this.current -= 10;
          }

          this.loading = false;
        });
      });
    },
    handleInfiniteOnLoad() {
      this.loading = true;
      this.current += 10;
      this.getPokemonList();
    },
    onSearch() {
      this.current = 0;
      this.data = [];
      if (this.pokemonName !== "") {
        let data = {
          name: this.pokemonName
        };
        this.$api.common
          .getPokemonInfo(data)
          .then(res => {
            this.data.push({
              data: res.data
            });
          })
          .catch(() => {
            this.$message.warning("请输入正确的宝可梦名称");
          });
      } else {
        this.getPokemonList();
      }
    },
    getAbilityList() {
      let data = {
        offset: 0,
        limit: 293
      };

      this.$api.common.getAbilityList(data).then(res => {
        console.log(res);
      });
    },
    addPokemon(item) {
      this.selectedPokemon.push(item);
    },
    removePokemon(index) {
      this.selectedPokemon.splice(index, 1);
    }
  }
};
</script>

